.list-view-item-details-container .inner-container {
  padding: 10px 18px; }
  @media screen and (min-width: 768px) {
    .list-view-item-details-container .inner-container {
      padding: 20px 40px; } }

.list-view-item-details-container hr {
  margin-bottom: 0; }

.list-view-item-details-container td,
.list-view-item-details-container a,
.list-view-item-details-container h6,
.list-view-item-details-container p,
.list-view-item-details-container span {
  font-size: 12px;
  font-weight: bold; }
  @media screen and (min-width: 768px) {
    .list-view-item-details-container td,
    .list-view-item-details-container a,
    .list-view-item-details-container h6,
    .list-view-item-details-container p,
    .list-view-item-details-container span {
      font-size: 14px; } }

@media screen and (min-width: 768px) {
  .list-view-item-details-container .table-container:nth-of-type(1) {
    padding-right: 20px; }
  .list-view-item-details-container .table-container:nth-of-type(2) {
    padding-left: 20px; } }

.list-view-item-details-container td {
  padding: 10px 0 0 0; }

.list-view-item-details-container .title {
  color: #2C3652;
  margin-bottom: 10px; }

.list-view-item-details-container .desc {
  font-weight: normal;
  color: #6E7F95; }

.list-view-item-details-container .value {
  color: #6E7F95;
  text-align: right; }

.list-view-item-details-container .fa-check {
  color: #6480EB; }

.list-view-item-details-container .fa-times {
  color: #FF6560; }
