.header-list-view-container {
  max-width: 854px;
  margin-bottom: 8px;
  padding: 15px 0 0 0;
  background: #FFFFFF;
  /* Mild shadow */
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05); }
  @media screen and (max-width: 767px) {
    .header-list-view-container {
      padding-top: 10px; } }
  @media screen and (max-width: 991px) {
    .header-list-view-container {
      max-width: none; } }
  .header-list-view-container .info-container .compare-container .compare-price {
    padding-left: 15px; }
    @media screen and (max-width: 767px) {
      .header-list-view-container .info-container .compare-container .compare-price {
        margin: 10px 0 5px 0; } }
  .header-list-view-container .info-container .compare-container .compare {
    margin-left: 20px;
    display: inline-block; }
    @media screen and (max-width: 767px) {
      .header-list-view-container .info-container .compare-container .compare {
        margin-left: 6px;
        display: block; } }
  .header-list-view-container .info-container .compare-container .compare-extra {
    margin-left: 39px; }
    @media screen and (max-width: 767px) {
      .header-list-view-container .info-container .compare-container .compare-extra {
        margin-left: 15px; } }
  .header-list-view-container .info-container .insur-container {
    padding-left: 39px;
    display: flex;
    align-items: center; }
    @media screen and (max-width: 767px) {
