          height: 50px; } }
    .header-list-view-container .info-container .insur-container .header-inner {
      position: relative; }
      @media screen and (max-width: 767px) {
        .header-list-view-container .info-container .insur-container .header-inner {
          width: 100%;
          align-items: baseline;
          display: flex; } }
      .header-list-view-container .info-container .insur-container .header-inner button {
