      .header-list-view-container .info-container .detail-container {
        padding-left: 15px; } }
    .header-list-view-container .info-container .detail-container .detail-inner-wrapper:not(:last-of-type) {
      margin-right: 40px; }
    .header-list-view-container .info-container .detail-container .detail-inner {
      display: flex;
      flex-grow: 1; }
